*{
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}
body{
  background: #699dde;
}
.container{
  width: 100%;
  max-width: 400px;
  padding: 40px 0 0 0;
  margin: 0 auto;
}
.container input { 
  width: 100%;
  padding: 14px;
  border:none;
  outline: none;
  border:1px solid #f3f3f3;
}
.container .data .row{
  width:100%;
  padding:14px;
  color:#fff;
  background: #333;
  margin: 12px 0 0 0;
}